import { arrayIncludes } from '../utils/array'
import { keys } from '../utils/object'


  export function useRootWhisperer() {
    let root: any = null;
    let registrar = {};

    function rootSet(proxyRoot: any){
      root = proxyRoot;
    };

    function removeListeners(){
      // Unregister all registered listeners
      keys(registrar || {}).forEach(event => {
        registrar[event].forEach((callback: (...args: any[]) => any) => {
          listenOffRoot(event, callback);
        })
      })

      registrar = null
    };

     function registerRootListener(event: string, callback: (...args: any[]) => any) {
       if (registrar) {
        registrar[event] = registrar[event] || [];
         if (!arrayIncludes(registrar[event], callback)) {
          registrar[event].push(callback);
         }
       }
     };

     function unregisterRootListener(event: string, callback: (...args: any[]) => any) {
       if (registrar && registrar[event]) {
        registrar[event] = registrar[event].filter(cb => cb !== callback);
       }
     };

     function listenOnRoot(event: string, callback: (...args: any[]) => any) {
       if (root) {
         root.$on(event, callback);
         registerRootListener(event, callback);
       }
     };

     function listenOnRootOnce(event: string, callback: (...args: any[]) => any, ...args: any[]) {
       if (root) {
         const _callback = (event: string, callback: (...args: any[]) => any) => {
           unregisterRootListener(event, callback);
           // eslint-disable-next-line node/no-callback-literal
           callback(...args);
         }

         root.$once(event, _callback);
         registerRootListener(event, _callback);
       }
     };

     function listenOffRoot(event: string, callback: (...args: any[]) => any) {
       unregisterRootListener(event, callback)

       if (root) {
         root.$off(event, callback)
       }
     };

     function emitOnRoot(event: string, ...args: any[]) {
       if (root) {
         root.$emit(event, ...args);
       }
     };

     return {
       listenOnRoot,
       emitOnRoot,
       rootSet,
       registerRootListener,
       removeListeners
     }
 }

